import React from "react";
import { withRouter } from "react-router";
import { Switch, Route, Redirect } from "react-router-dom";
import AsyncComponent from "components/common/AsyncComponent";
import QueryString from "utils/query-string";
import VConsole from "vconsole";

const Index = AsyncComponent(() => import("components/IndexPage"));
const Verify = AsyncComponent(() => import("components/VerifyPage"));
const Progress = AsyncComponent(() => import("components/ProgressPage"));
const My = AsyncComponent(() => import("components/MyPage"));
const Feedback = AsyncComponent(() => import("components/FeedbackPage"));
const Report = AsyncComponent(() => import("components/ReportPage"));
const Example = AsyncComponent(() => import("components/ExamplePage"));

class App extends React.PureComponent {
  componentDidMount() {
    const query = QueryString.parse(this.props.location.search);
    if (!!query.test && +query.test === 1) {
      new VConsole();
    }
  }
  render() {
    return (
      <Switch>
          <Route exact path="/" render={props => <Index {...props} />} />
          <Route
            exact
            path="/verify"
            component={props => <Verify {...props} />}
          />
          <Route
            exact
            path="/progress/:step"
            render={props => <Progress
              {...props}
            />
            }
          />
          <Redirect
            from="/progress"
            to={{
              pathname: "/progress/1",
              search: this.props.location.search
            }}
          />
          <Route
            exact
            path="/report"
            component={props => <Report {...props} />}
          />
          <Route
            exact
            path="/my/:type"
            render={props => <My {...props} />
            }
          />
          <Redirect
            from="/my"
            to={{
              pathname: "/my/reports",
              search: this.props.location.search
            }}
          />
          <Route
            path="/feedback"
            render={props => <Feedback {...props} />
            }
          />
          <Route path="/example" component={Example} />
        </Switch>
    );
  }
}

export default withRouter(App);
