import React from "react";
import { render } from "react-dom";
import { HashRouter } from "react-router-dom";
import { createGlobalStyle } from "styled-components";
import App from "./App";
import Cookie from "utils/cookie-operate";
import * as serviceWorker from "./serviceWorker";

try {
  window.localStorage.setItem("isPrivateMode", "1");
  window.localStorage.removeItem("isPrivateMode");
  window.supportLS = true;
} catch (e) {
  window.supportLS = false;
}

const cookie = new Cookie();

const app = document.getElementById("app");
if (window.supportLS) {
  window.localStorage.setItem(
    "$WH",
    document.documentElement.clientHeight || document.body.clientHeight
  );
} else {
  cookie.set(
    "$WH",
    document.documentElement.clientHeight || document.body.clientHeight
  );
}

window.addEventListener("resize", () => {
  const WH =
    document.documentElement.clientHeight || document.body.clientHeight;
  document.activeElement.scrollIntoView();
  const $WH = window.supportLS
    ? window.localStorage.getItem("$WH")
    : cookie.get("$WH");
  if ($WH - WH > 100) {
    app.style.height = `${$WH}px`;
  } else {
    app.style.height = "100%";
  }
});

const GlobalStyle = createGlobalStyle`
    * {
        font-family: "Microsoft YaHei";
        box-sizing: border-box;
        outline: none;
        -webkit-overflow-scrolling: touch;
    }

    html, body {
        margin: 0;
        padding: 0;
        overflow-x: hidden;
    }

    body {
        background-color: #FFFFFF !important;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
    }

    html, body, #app {
        height: 100%;
        position: relative;
    }

    a {
        text-decoration: none;
    }

    input, textarea {
        -webkit-appearance: none;
    }

    div {
        -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    }

    ul {
        margin: 0;
        padding: 0;
        list-style: none;
    }

    p {
        margin: 0;
    }

    em {
        font-style: normal;
    }

    .am-checkbox-wrapper {
        height: 0.45rem;
        margin-right: 0.1334rem;

        .am-checkbox {
            width: 0.45rem;
            height: 100%;
            vertical-align: top;

            .am-checkbox-input {
                margin: 0;
            }

            .am-checkbox-inner {
                width: 100%;
                height: 100%;
                border-radius: 3px;

                &:after {
                    width: 0.1334rem;
                    height: 0.2934rem;
                    top: 0;
                    right: 0.12rem;
                }
            }
        }
    }

    .am-toast-notice-content .am-toast-text {
        min-width: 1.6rem;
        padding: 0.24rem 0.4rem;
    }

    .am-toast-notice-content .am-toast-text.am-toast-text-icon {
        border-radius: 0.1334rem;
        padding: 0.4rem;
    }

    .am-toast-notice-content .am-toast-text.am-toast-text-icon .am-toast-text-info {
        margin-top: 0.16rem;
    }

    .am-toast {
        font-size: 0.3734rem;
    }

    .am-icon {
        width: 0.5867rem;
        height: 0.5867rem;
    }

    .am-icon-lg {
        width: 0.96rem;
        height: 0.96rem;
    }

    .am-picker-popup-item {
        height: 1.12rem !important;
        padding: 0.24rem 0.4rem !important;
        font-size: 0.4267rem !important;
    }

    .am-picker-col-item {
        height: 0.9067rem !important;
        font-size: 0.4267rem !important;
        line-height: 0.9067rem !important;

        &.selected{ 
            font-size: 0.4534rem !important;
        }
    }

    .am-picker-col-indicator {
        height: 0.9067rem !important;
    }

    .am-modal-transparent {
        width: 7.2rem !important;
        top: 4.8rem;
        left: 50% !important;
        margin-left: -3.6rem;
        position: absolute !important;

        .am-modal-content {
            padding-top: 0.4rem !important;

            .am-modal-header {
                padding: 0.16rem 0.4rem 0.4rem !important;

                .am-modal-title {
                    font-size: 0.48rem !important;
                }
            }

            .am-modal-body {
                padding: 0 0.4rem 0.4rem !important;
                font-size: 0.4rem !important;
            }
        }

        .am-modal-button {
            height: 1.3334rem !important;
            font-size: 0.4267rem !important;
            line-height: 1.3334rem !important;
        }
    }

    .am-tabs-default-bar-top .am-tabs-default-bar-tab, .am-tabs-default-bar-bottom .am-tabs-default-bar-tab {
        padding: 0.2134rem 0 !important;
    }

    .am-tabs-default-bar-tab {
        height: 1.1734rem !important;
        font-size: 0.3734rem !important;
        line-height: 1.1734rem !important;
    }
`;

render(
  <>
    <HashRouter>
      <App />
    </HashRouter>
    <GlobalStyle />
  </>,
  app
);

serviceWorker.unregister();
